import React, {Component} from 'react';
import Fade from 'react-reveal/Fade'
class About extends Component {
  render() {
    return (
      <Fade bottom cascade duration={500} distance={'20px'}>
      <div className='flex-container'>

        <div className='content'>
          <h3>About Me</h3><br/>
          <div className='about-flex-container'>
          <div className='overlay'>
          <img src={require('../about-image.jpg')} width="200px"/>
          </div>
          <div className='about-paragraphs'>
          <div >
              <p>Hello! I'm CJ, a computer engineer based in the Philippines who enjoys building things that live on the internet. I develop websites and web apps that provide intuitive, great user experience and user interface.</p><br/>
              <p>I joined the development team at <a className='contentlinks' href="http://decodetokyo.com/" target="_blank" rel="nofollow noopener noreferrer">DecodeTokyo</a> where I work on a wide variety of interesting and meaningful projects.</p><br/>
              <p>Here are a few technologies I've been working with recently:</p><br/>
            </div>
            <ul className="about_skills">
              <li className="about_skills_items">JavaScript (ES6+)</li>
              <li className="about_skills_items">HTML &amp; (S)CSS</li>
              <li className="about_skills_items">React</li>
              <li className="about_skills_items">Vue</li>
              <li className="about_skills_items">Node.js</li>
              <li className="about_skills_items">Express</li>
              <li className="about_skills_items">MySQL</li>
              <li className="about_skills_items">PHP</li>
            </ul>
          </div>
          </div>
          
            
          </div>
        </div>
        </Fade>
    );
  }
}

export default About;