import React, {Component} from 'react';
import {faBehance,faGithub} from '@fortawesome/free-brands-svg-icons'
import {faExternalLinkAlt} from '@fortawesome/free-solid-svg-icons'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import image1 from '../f3eba587597025.5dbcd5b42adae.png';
import image2 from '../0bb51489073907.5de9556cd8868.png';
import image3 from '../4b797d87629215.5dbe40a11017e.png';
import image4 from '../567b6589187143.5ded711a88526.jpg';
import Fade from 'react-reveal/Fade'
export class Works extends Component {
  render() {
    return (
      <Fade bottom duration={400} distance={'20px'}>
      
      <div className='flex-container'>
        <div className='content'>
        <h3>Some Things I've Built</h3><br/>
        
          <div className='works-flex-container'>
          <Fade left duration={500} distance={'50px'}>
          <div className='project-odd'>
          <div className='overlay'>
          <a href='https://www.behance.net/gallery/87597025/MacMall-Redesign' target="_blank" rel="nofollow noopener noreferrer">
          <img src={image1} />
          </a>
          </div>
            <div className='details'>
              <h4>Featured Project</h4>
              <h3>Job Entrance Exam</h3>
              <div className='description'>
                <p>Initial web redesign for the MacMall Online Store. Redesigned the navigation, "On Sale" Counter, featured items, side navigation, categories bar and a sample item cards page</p>
              </div>
              <ul>
                <li>Adobe XD</li>
                <li>Adobe Illustrator</li>
              </ul>
              <div className='links'>
                <a href='https://www.behance.net/gallery/87597025/MacMall-Redesign' target="_blank" rel="nofollow noopener noreferrer"><FontAwesomeIcon icon={faBehance}/></a>
              </div>
            </div>
          </div>
          </Fade>
          <Fade right duration={500} distance={'50px'}>
          <div className='project-even'>
          <div className='overlay'>
          <a href='https://www.behance.net/gallery/89073907/Chess-World-Web-design' target="_blank" rel="nofollow noopener noreferrer">
          <img src={image2}/>
          </a>
          </div>
          
            <div className='details'>
              <h4>Featured Project</h4>
              <h3>ChessWorld Web Design</h3>
              <div className='description'>
                <p>A Website dedicated to Chess. Learn new moves, tactics and more. Solve Millions of challenging puzzles and learn from the masters in the art of strategy and tactics. </p>
              </div>
              <ul>
                <li>Adobe XD</li>
                <li>ReactJS</li>
                <li>SCSS</li>
              </ul>
              <div className='links'>
                <a href='https://www.behance.net/gallery/89073907/Chess-World-Web-design' target="_blank" rel="nofollow noopener noreferrer"><FontAwesomeIcon icon={faBehance}/></a>
              </div>
            </div>
          </div>
          </Fade>
          <Fade left duration={500} distance={'50px'}>
          <div className='project-odd'>
          <div className='overlay'>
          <a href='https://github.com/carljustineoyales/themescape' target="_blank" rel="nofollow noopener noreferrer">
          <img src={image3} />
          </a>
          </div>
            <div className='details'>
              <h4>Featured Project</h4>
              <h3>Themescape Wordpress Theme</h3>
              <div className='description'>
                <p>A simple web design for an ecommerce wordpress theme</p>
              </div>
              <ul>
                <li>Adobe XD</li>
                <li>Adobe Illustrator</li>
              </ul>
              <div className='links'>
                <a href='https://github.com/carljustineoyales/themescape' target="_blank" rel="nofollow noopener noreferrer"><FontAwesomeIcon icon={faGithub}/></a>
                <a href='https://www.behance.net/gallery/87629215/Online-Theme-Store' target="_blank" rel="nofollow noopener noreferrer"><FontAwesomeIcon icon={faBehance}/></a>
                
              </div>
            </div>
          </div>
          </Fade>
          <Fade right duration={500} distance={'50px'}>
          <div className='project-even'>
          <div className='overlay'>
          <a href='https://github.com/carljustineoyales/portfolio' target="_blank" rel="nofollow noopener noreferrer">
          <img src={image4}/>
          </a>
          </div>
          
            <div className='details'>
              <h4>Featured Project</h4>
              <h3>Portfolio v1</h3>
              <div className='description'>
                <p>First Web Portfolio Design. Web design using adobe XD, adobe Illustrator. </p>
              </div>
              <ul>
                <li>Adobe XD</li>
                <li>ReactJS</li>
                <li>SCSS</li>
              </ul>
              <div className='links'>
                <a href='https://github.com/carljustineoyales/portfolio' target="_blank" rel="nofollow noopener noreferrer"><FontAwesomeIcon icon={faGithub}/></a>
                <a href='https://www.behance.net/gallery/89187143/My-Portfolio-Web-Design' target="_blank" rel="nofollow noopener noreferrer"><FontAwesomeIcon icon={faBehance}/></a>
                <a href='https://github.com/carljustineoyales/portfolio' target="_blank" rel="nofollow noopener noreferrer"><FontAwesomeIcon icon={faExternalLinkAlt}/></a>
              </div>
            </div>
          </div></Fade>
          
          
          {/* <button className='btn btn-contact'>Show More</button> */}
          </div>
          
        </div>
      </div>
      </Fade>
    );
  }
}

export default Works;
