import React, {Component} from 'react';
import Fade from 'react-reveal/Fade'
class Contact extends Component {
  render() {
    return (
      <Fade bottom cascade duration={500} distance={'20px'}>

      <div className='flex-container'>
        <div className='content'>
        <div className='contact-flex-container'>   
        <h3>What's Next?</h3><br/> 
          <h1>Let's Talk</h1><br/> 
          <p>Although I'm not currently looking for freelance opportunities, my inbox is always open. Whether for a potential project or just to say hi, I'll try my best to answer your email!</p><br/> 
          <a href='mailto:carljustineoyales@gmail.com' className='btn btn-contact'>Send Me A Message</a>
        </div>
        </div>
      </div>
      </Fade>
    );
  }
}

export default Contact;