import React, {Component} from 'react';
import {faBars, faTimes} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {Link} from 'react-scroll'
import logo from '../logo.svg'
import Fade from 'react-reveal/Fade';
class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    }
    this.handleOnClick = this
      .handleOnClick
      .bind(this)
  }

  handleOnClick = (event) => {
    console.log('clicked')
    this.setState({
      show: !this.state.show
    })
  }

  render() {
    return (
      <nav>
        <Fade top cascade duration={500}>
          <a href='/reactjs-portfolio'><img src={logo} width='28px'/></a>
        </Fade>
        <Fade top cascade duration={500}>
          <ul>
            <li>
              <Link className='navlinks' to='about' spy={true} smooth={true} offset={0} duration={500}>About</Link>
            </li>
            <li>
              <Link className='navlinks' to='experience' spy={true} smooth={true} offset={0} duration={500}>Experience</Link>
            </li>
            <li>
              <Link className='navlinks' to='works' spy={true} smooth={true} offset={0} duration={500}>Works</Link>
            </li>
            <li>
              <Link className='navlinks' to='contact' spy={true} smooth={true} offset={0} duration={500}>Contact</Link>
            </li>

            <li>
              <a
                href='https://drive.google.com/file/d/1FvnuDINFQ2OCFlawBxFdTC-op5KxsZ9L/view?usp=sharing'
                target="_blank"
                rel="nofollow noopener noreferrer"
                className='btn'>Resume</a>
            </li>
          </ul>
        </Fade>

        {(this.state.show)
          ? (
            <Fade top cascade duration={500}>
              <span
                style={{
                zIndex: '99999'
              }}
                onClick={this.handleOnClick}><FontAwesomeIcon icon={faTimes}/></span>
            </Fade>
          )
          : (
            <Fade top cascade duration={500}>
              <span onClick={this.handleOnClick}><FontAwesomeIcon icon={faBars}/></span>
            </Fade>

          )}
        <Fade right when={this.state.show}>
          <div
            className='drawer-nav'
            style={(this.state.show)
            ? ({display: 'inline'})
            : ({display: 'none'})}>
            <ul className='drawer-nav-item-container'>
              <li>
                <Link
                  onClick={this.handleOnClick}
                  className='navlinks'
                  to='about'
                  spy={true}
                  smooth={true}
                  offset={0}
                  duration={500}>About</Link>
              </li>
              <li>
                <Link
                  onClick={this.handleOnClick}
                  className='navlinks'
                  to='experience'
                  spy={true}
                  smooth={true}
                  offset={0}
                  duration={500}>Experience</Link>
              </li>
              <li>
                <Link
                  onClick={this.handleOnClick}
                  className='navlinks'
                  to='works'
                  spy={true}
                  smooth={true}
                  offset={0}
                  duration={500}>Work</Link>
              </li>
              <li>
                <Link
                  onClick={this.handleOnClick}
                  className='navlinks'
                  to='contact'
                  spy={true}
                  smooth={true}
                  offset={0}
                  duration={500}>Contact</Link>
              </li>
              <li>
                <a
                  onClick={this.handleOnClick}
                  href='https://drive.google.com/file/d/1FvnuDINFQ2OCFlawBxFdTC-op5KxsZ9L/view?usp=sharing'
                  target="_blank"
                  rel="nofollow noopener noreferrer"
                  className='btn'>Resume</a>
              </li>
            </ul>
          </div>
        </Fade>
      </nav>
    );
  }
}

export default Navbar;