import React, {Component} from 'react';
import Fade from 'react-reveal/Fade';
import Typewriter from 'typewriter-effect';
class HeroBanner extends Component {
  render() {
    return (
      <Fade bottom cascade duration={500} distance={'20px'}>
      <div className='flex-container'>
       
          <div className='headers'>
            <p>Hi! my name is</p>
            <h1>Carl Justine Oyales</h1>
            <h2><Typewriter
  options={{
    strings: ["Computer Engineer", "Web Designer","Front End Developer"],
    autoStart: true,
    loop: true,
  }}
/></h2>
          </div>

          <div className='hero_description'>
            <p >I'm a Computer Engineer based in the Philippines specializing in building <span>(and occasionally designing)</span> websites and applications.</p>
          </div>
          
          <a href='mailto:carljustineoyales@gmail.com' className='btn btn-contact'>Send Me A Message</a>
          
      </div>
      </Fade>
    );
  }
}

export default HeroBanner;