import React, {Component} from 'react';
import Fade from 'react-reveal/Fade'
class Experience extends Component {

  constructor(props) {
    super(props);
    this.state = {
      company: 'decodetokyo'
    }
    this.handleOnClick = this
      .handleOnClick
      .bind(this)
  }

  handleOnClick = (event) => {
    event.preventDefault();
    this.setState({company: event.target.value})
  }

  showDetails = () => {
    switch (this.state.company) {
      case 'decodetokyo':
        return (
          <>
            <h4>Front End Developer <br/><a className='contentlinks' href="http://decodetokyo.com/" target="_blank" rel="nofollow noopener noreferrer">DecodeTokyo</a>
            </h4>
            <h5>Jan 2020 - Present</h5><br/>
            <ul className="decodetokyo">

              <li className="decodetokyo_items">Determining the structure and design of web pages.</li>
              <li className="decodetokyo_items">Developing features to enhance the user experience</li>
              <li className="decodetokyo_items">Ensuring web design is optimized for smartphones.</li>
              <li className="decodetokyo_items">Building reusable code for future use.</li>
            </ul><br/>
         </>
        )
      case 'kdci':
        return (
 <>
            <h4>Web Graphics Designer <br/> 
            <a className='contentlinks' href="https://kdci.co" target="_blank" rel="nofollow noopener noreferrer">KDCI Outsourcing</a>
            </h4>
            <h5>Aug 2019 - Nov 2019</h5><br/>
            <ul className="kdci">
            
              <li className="kdci_items">Develop creative ideas and concepts, choosing the
                appropriate media and style to meet design objectives</li>
              <li className="kdci_items">Creates designs solutions that has high visual impact</li>
              <li className="kdci_items">Communicate concepts and design reasoning clearly and professionally</li>
              <li className="kdci_items">Skilfully create original, strategic design solutions from concept to completion</li>
              <li className="kdci_items">Collaborate in brainstorming and idea generation meetings</li>
            </ul><br/>
      </>
        )
      case 'pmpc':
        return (
          <>
            <h4>Production Engineer Intern <br/>
            <a className='contentlinks' href="https://www.panasonic.com/ph" target="_blank" rel="nofollow noopener noreferrer">Panasonic Manufacturing Phil. Corp.</a>
            </h4>
            <h5>Apr 2018 - May 2018</h5><br/>
            <ul className="pmpc">

              <li className="pmpc_items">Conducts line monitoring to improve quality</li>
              <li className="pmpc_items">Validates compliance to Procedures, Guidelines,
                Operation Standards, Quality Principle, PQC Rule and ISO Standards</li>
              <li className="pmpc_items">Generates and reviews Operation Standards,
                Guidelines, Procedures and Forms/Checksheets as necessary related to inspection</li>
              <li className="pmpc_items">Analyzes problems encountered by Production and find
                ways on how to prevent the re-occurence</li>
              <li className="pmpc_items">Leas the group towards implementation of improvement
                activities that will help promore better quality and productivity</li>
            </ul><br/>
        </>
        )

      default:
        break;
    }
  }

  render() {
    return (
      <Fade bottom cascade duration={500} distance={'20px'}>

      <div className='flex-container'>
        <div className='experience content'>
          <h3>Where I've Worked</h3>
          <div className='experience-flex-container'>
          <div className='experience-nav'>
            <button className={`btn experience-nav-items ${this.state.company === 'decodetokyo' ? `btn-active`: ''}`} onClick={this.handleOnClick} value='decodetokyo'>DecodeTokyo</button>
            <button className={`btn experience-nav-items ${this.state.company === 'kdci' ? `btn-active`: ''}`} onClick={this.handleOnClick} value='kdci'>Key Discovery Consultation Inc.</button>
            <button className={`btn experience-nav-items ${this.state.company === 'pmpc' ? `btn-active`: ''}`} onClick={this.handleOnClick} value='pmpc'>Panasonic Manufacturing Corp.</button>
          </div>
          <div className="experience-container">
            {this.showDetails()}
          </div>
            
          </div>
        </div>
      </div>
      </Fade>
    );
  }
}

export default Experience;